.Countdown {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Countdown h1 {
    color: #162C4E;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    font-size: 72px;
    letter-spacing: -1px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.Countdown svg {
    position: absolute;
    left: 0px;
    bottom: -40px;
}

.digits {
    min-width: 132px;
    position: relative;
}

@media (max-width: 768px) {
    .Countdown h1 {
        font-size: 37px;
    }
    .digits {
        min-width: 82px;
        position: relative;
    }
    .digits svg {
        left: 10px;
        width: 64px;
        transform: scaley(1.5);
    }
    .digits svg text {
        font-weight: 400;
        font-size: 18px;
        transform: scaleX(1.2) translateX(-12px);
    }
}

@media (max-width: 360px) {
    .Countdown {
        margin-top: 20px;

    }
    .digits {
        min-width: 70px;
    }
    .digits svg {
        left: 5px;
    }
}
