.Header {
    height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media (max-width: 768px) {
    .Header {
        padding-top: 50px;
        margin-bottom: 65px;
    }
}

@media (max-width: 360px) {
    .Header {
        height: 15vh;
        padding-top: 40px ;
        margin-bottom: 20px;
    }
    .Header svg{
        transform: scale(0.67);
    }   
}