.Footer {
    background-color: #162C4E;
    height: 190px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    margin-top: auto;
}

.Footer form {
    min-width: 440px;
    display: flex;
}

.Footer input, button {
    height: 60px;
    border: 0;
    outline: none;
}

.Footer input {
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    width: 100%;
    padding: 0px 0px 0px 25px;
    border-radius: 40px 0px 0px 40px;
}

.Footer button {
    height: 43px;
    width: 43px;
    background-color: white;
    padding: 0px;
    border-radius: 40px;
    cursor: pointer;
}

.submit-btn {
    display: flex;
    align-items: center;
    background-color: white;
    padding: 0px 10px 0px 0px;
    border-radius: 0px 40px 40px 0px ;
}

@media (max-width: 768px) {
    .Footer {  
        height: 160px;
    }
}

@media (max-width: 450px) {
    .Footer form {
        min-width: 380px;
        max-width: 380px;
    }
  }

@media (max-width: 400px) {
    .Footer form {
        min-width: 280px;
        max-width: 280px;
    }
    .Footer input {
        font-weight: 400;
        font-family: 'Poppins', sans-serif;
        font-size: 14px;
        letter-spacing: -1px;
        width: 100%;
        padding: 0px 0px 0px 15px;
        border-radius: 40px 0px 0px 40px;
    }
  }

  @media (max-width: 330px){
    .Footer form {
        min-width: 200px;
        max-width: 200px;
    }
  }