.Maintenance { 
    height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: -12px;
}

.Maintenance h1 {
    color: #A4ADBA;
    font-family: 'Poppins', sans-serif;
    font-size: 72px;
    letter-spacing: 0px;
    margin: 0px
}

.Maintenance p {
    font-family: 'Poppins', sans-serif;
    max-width: 423px;
    font-size: 18px;
    margin-top: -10px;
}

@media only screen and (max-width: 1120px) {
    .Maintenance h1 {
        font-size: 60px;
    }
}

@media only screen and (max-width: 880px) {
    .Maintenance h1 {
        font-size: 48px;
    }
}

@media (max-width: 768px) {
    .Maintenance { 
        height: 20vh;
    }

    .Maintenance h1 {
        font-size: 36px;
    }
    .Maintenance p {
        max-width: 286px;
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 16px;
    }
}

@media (max-width: 360px) {
    .Maintenance { 
        height: 20vh;
    }

    .Maintenance h1 {
        font-size: 36px;
        line-height: 1em;
    }
    .Maintenance p {
        max-width: 286px;
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: 16px;
    }
}