.Popup {
    z-index: 1;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100vh;
    width: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: rgba(15, 27, 53, 0.473);
}

.Popup.active {
        display: flex;
}


.modal {

    font-weight: 400;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    background-color: white;
    width: 320px;
    height: 370px;
}

.cross-btn {
    height: 55px;
    text-align: right;
}
.cross-btn button {
    margin: 15px;
    cursor: pointer;
    padding: 0px;
    background-color: white;
    height: 25px;
    width: 25px;
}

.modal h1 {
    color: #162C4E;
}

.modal p {
    font-size: 16px;
    padding: 0px 20px;
}

.close-btn {
    font-size: 18px;
    font-weight: 300;
    font-family: 'Poppins', sans-serif;
    margin-top: 10px;
    background-color: #1D4169;
    color: white;
    height: 60px;
    width: 200px;
    border-radius: 40px;
    appearance: none;
    border-style: none;
    box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px,rgba(0, 0, 0, .14) 0 6px 10px 0,rgba(0, 0, 0, .12) 0 1px 18px 0;
    box-sizing: border-box;
    cursor: pointer;
    overflow: visible;
    transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: transform,opacity;
    z-index: 0;
}

.close-btn:hover {
    background-color: #345f91;
}

.close-btn:active {
  box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
  outline: none;
}

.close-btn:focus {
  outline: none;
  border: 2px solid #4285f4;
}

@media only screen and (max-width: 768px) {
    .Popup {
        height: 1024px;
    }
}